<!-- 评论box -->
<template>
  <div class="comment-box">
    <div class="avatar">
      <img src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png" alt />
    </div>
    <div class="info">
      <div class="name">
        <span>{{parent.userName}}</span>
        <!-- <el-rate v-if="showRate" disabled v-model="value1"></el-rate> -->
      </div>
      <p class="content">
        {{parent.commentContext}}
      </p>
      <div class="time">
        <span>{{parent.createtime | formatDate}}</span>
        <i @click="handleShowInput" class="iconfont icon-chat"></i>
      </div>
      <!-- 点击评论 -->
      <template  v-if="parent.showInput">
        <div key="parent" class="repeat">
          <el-input resize="none" type="textarea" v-model="parentText" maxlength="200"></el-input>
          <div @click="handleRepeatParent" class="repeat-btn">回复</div>
        </div>
      </template>
      <!-- 若有二级评论 -->
      <template v-if="resultArray && resultArray.length > 0">
        <div class="sub-comment">
          <div v-for="item in resultArray" :key="item.commentId" class="item">
            <div class="sub-avatar">
              <img src="https://cube.elemecdn.com/0/88/03b0d39583f48206768a7534e55bcpng.png" alt />
            </div>
            <div class="sub-info">
              <div class="name">{{item.userName}}</div>
              <div
                class="content"
              >回复 {{item.parentUserName}}:{{item.commentContext}}</div>
              <div class="time">
                <span>{{item.createtime|formatDate}}</span>
                <i @click="handleShowInputChildren(item)" class="iconfont icon-chat"></i>
              </div>
              <!-- 点击评论 -->
              <template v-if="item.showInput">
                <div class="repeat">
                  <el-input resize="none" type="textarea" v-model="item.text" maxlength="200"></el-input>
                  <div @click="handleRepeat(item)" class="repeat-btn">回复</div>
                </div>
              </template>
            </div>
          </div>
        </div>
      </template>
    </div>
  </div>
</template>

<script>
import {dateFormat} from '@/assets/javascript/ultis'
export default {
  props: {
    showRate: {
      type: Boolean,
      default: true
    },
    info: {
      type: Object
    }
  },
  data() {
    return {
      /* start */
      parent:{},
      resultArray:[],
      parentText:''
    };
  },
  filters:{
    formatDate(val) {
      return dateFormat(new Date(val), "yyyy-MM-dd")
    }
  },
  watch:{
    info(newValue){
      if(newValue) {
        this.parent = JSON.parse(JSON.stringify(this.info));
        this.formatInfoChildren(this.parent.sons)
      }
    }
  },
  created(){
    this.parent = JSON.parse(JSON.stringify(this.info));
    this.formatInfoChildren(this.parent.sons)
  },
  methods:{
    handleRepeatParent(){
      let str = this.parentText.replace(/^\s*|\s*$/g,"");
      if(!str) {
        return this.$myLayer.dangerLayer("请输入评论信息")
      }
      const params = {
        attributeObj:this.parent.attributeObj,
        context: this.parentText,
        parentId:this.parent.commentId,
        parentUserId:this.parent.opUserId
      }
      this.$api.addComment(params).then(res => {
        this.parentText = '';
        this.$emit("commentOver")
      })
    },
    handleRepeat(item){
      let str = item.text.replace(/^\s*|\s*$/g,"");
      if(!str) {
        return this.$myLayer.dangerLayer("请输入评论信息")
      }
      const params = {
        attributeObj:item.attributeObj,
        context: item.text,
        parentId:item.commentId,
        parentUserId:item.opUserId
      }
      this.$api.addComment(params).then(res => {
        this.parentText = '';
        this.$emit("commentOver")
      })
    },
    formatInfoChildren(targetArray){
      for(let item of targetArray) {
        this.resultArray.push(item);
        if(item.sons && item.sons.length>0){
          this.formatInfoChildren(item.sons)
        }
      }
    },
    handleShowInput(){
      this.$set(this.parent,"showInput",true)
    },
    handleShowInputChildren(item){
      this.$set(item,"showInput",true)
    }
  }
};
</script>

<style lang="scss" scoped>
.comment-box {
  display: flex;
  margin-top: 30px;
  .avatar {
    width: 48px;
    height: 48px;
    img {
      width: 48px;
      height: 48px;
      border-radius: 50%;
    }
  }
  .info {
    flex: 1;
    margin-left: 20px;
    padding-bottom: 30px;
    border-bottom: 1px solid #e0e4eb;
    .name {
      display: flex;
      & > span {
        margin-right: 20px;
        color: #303133;
      }
    }
    .content {
      color: #606266;
      margin: 20px 0;
    }
    .time {
      display: flex;
      align-items: center;
      line-height: 24px;
      span {
        color: #606266;
        display: inline-block;
        color: inherit;
        width: 120px;
      }
      i {
        margin-left: 50px;
        font-size: 20px;
        color: inherit;
        cursor: pointer;
      }
    }
    .repeat {
      margin-top: 10px;
      text-align: right;
      .repeat-btn {
        display: inline-block;
        margin-top: 10px;
        cursor: pointer;
        width: 100px;
        height: 36px;
        // background: #ebebeb;
        background: #2695ff;
        color: #fff;
        line-height: 36px;
        text-align: center;
        border-radius: 4px;
      }
    }

    .sub-comment {
      position: relative;
      margin-top: 25px;
      padding: 20px 30px 0;
      width: 100%;
      border-radius: 8px;
      background: rgba(242, 243, 244, 0.8);
      & > div:last-child {
        border-bottom: none;
      }
      .item {
        display: flex;
        margin-bottom: 20px;
        padding-bottom: 20px;
        border-bottom: 1px solid #e0e4eb;
        .sub-avatar {
          width: 48px;
          height: 48px;
          img {
            width: 48px;
            height: 48px;
            border-radius: 50%;
          }
        }
        .sub-info {
          flex: 1;
          margin-left: 20px;
          .time {
            i {
              margin-left: 0;
            }
          }
        }
      }
    }
    .sub-comment::before {
      content: "";
      position: absolute;
      width: 0;
      height: 0;
      border: 20px solid transparent;
      border-bottom-color: rgba(242, 243, 244, 0.8);
      //  background: rgba(242, 243, 244, 0.8);
      left: 30px;
      top: -30px;
    }
  }
}
</style>